import {
  IonBackButton,
  IonButton,
  IonIcon,
  IonButtons,
  IonContent,
  IonHeader,
  IonItem,
  IonLabel,
  IonPage,
  IonText,
  IonGrid,
  IonRow,
  IonCol,
  IonToolbar,
  IonToggle,
  useIonViewWillEnter,
  useIonAlert,
  IonTitle
} from '@ionic/react';
import React, {useState} from 'react'
import { LocalNotifications } from '@capacitor/local-notifications';
import {DatePicker as datePicker} from '@ionic-native/date-picker'
import {useGetProfileQuery} from '../../schema/types-and-hooks';
import {storageHelper} from '../../utils/storageHelper'

import './Reminder.scss'

const Reminder: React.FC = () => {
  const {data} = useGetProfileQuery();
  const [checked, setChecked] = useState(false);
  const [present] = useIonAlert();
  let reminder;
  let reminderTime;

  const onChangeReminder = (event) => {
    // console.log(event)
    if (event.detail.checked === false) {
      storageHelper.removeNativeStorage('reminder')
      reminder = false
      reminderTime = undefined
      LocalNotifications.cancel({notifications: [{id: 42}]})
    } else if (event.detail.checked) {
      setReminder()
    } else {
      storageHelper.removeNativeStorage('reminder')
      reminderTime = undefined
      LocalNotifications.cancel({notifications: [{id: 42}]})
    }
  }

 const setReminderTime = async (date) => {
    const time = new Date(date.getTime())
    const notifs = await LocalNotifications.schedule({
      notifications: [
        {
          id: 42,
          title: 'SpeakyPeaky',
          body: '20 mins a day = big gains in 3 months',
          schedule: {
            repeats: true,
            every: 'day',
            on: {
              hour: time.getHours(),
              minute: time.getMinutes(),
            },
          },
          sound: null,
          attachments: null,
          actionTypeId: '',
          extra: null,
        },
      ],
    })
  }
  const setReminder = async () => {
    try {
      const date = await datePicker.show({
        date: new Date(),
        mode: 'time',
        androidTheme: datePicker.ANDROID_THEMES.THEME_DEVICE_DEFAULT_LIGHT,
      })

      storageHelper.setNativeStorage('reminder', date.getTime())

      await setReminderTime(date)
      reminderTime = date.getTime()
      reminder = true

      present({
        header: 'Reminder is set!',
        message:
          'Perfect. We will send you a nudge at <strong>' +
          `${date}` +
          '</strong> everyday',
        cssClass: 'user-level-alert',
        buttons: ['OK'],
      })

    } catch (err) {
      // console.log('Error occurred while getting date: ', err)
    }
  }

  useIonViewWillEnter(() => {
    LocalNotifications.requestPermissions()
    Promise.all([storageHelper.getNativeStorage('reminder')]).then(([reminder]) => {
      if (reminder?.data) {
        reminderTime = new Date(reminder.data).toISOString()
        reminder = true
        setReminderTime(new Date(reminder.data))
      }
    })
  })

  return (
    <IonPage>
      <IonHeader mode="ios">
        <IonToolbar mode="ios">
          <IonButtons slot="start">
            <IonButton mode="md" routerLink="/setting">
              <IonIcon
                color="dark"
                mode="md"
                slot="icon-only"
                name="chevron-back"
              ></IonIcon>
            </IonButton>
            
          </IonButtons>
          <IonTitle>
              <div style={{textAlign: 'center'}}>
              <b>Reminder</b>
              </div>
          </IonTitle>
          <IonButtons slot="end">
            <IonButton mode="md" routerLink="/dashboard/lessons" routerDirection="root">
              <IonIcon
                color="dark"
                mode="md"
                slot="icon-only"
                src="./assets/images/home.svg"
              ></IonIcon>
            </IonButton>
          </IonButtons>
        </IonToolbar>
      </IonHeader>

      <IonContent className="ion-padding">
        <IonGrid >
          {/* <IonRow className="ion-align-items-center ion-justify-content-center">
            <IonCol
              sizeXs="12"
              sizeSm="12"
              sizeLg="6"
              sizeMd="7"
              sizeXl="4"
              className="ion-align-self-center"
            >
              <h1 className="ion-no-margin">
                <strong>Reminder</strong>
              </h1>
            </IonCol>
          </IonRow> */}

          <IonRow className="reminder-block ion-align-items-center">
            <IonCol className="ion-align-self-center">
              { reminderTime === undefined ? 
              <IonRow className="ion-align-items-center ion-justify-content-center ion-text-center">
                <IonCol
                  sizeXs="12"
                  sizeSm="12"
                  sizeLg="6"
                  sizeMd="7"
                  sizeXl="4"
                  className="ion-align-self-center"
                >
                  <IonIcon
                    className="alarm"
                    src="../../../../../assets/images/alarm.svg"
                  ></IonIcon>
                  <h3 className="ion-text-center ion-margin-bottom">
                    Would you like to receive a reminder?
                  </h3>
                </IonCol>
              </IonRow>
              :
              <IonRow className="ion-align-items-center ion-justify-content-center">
                <IonCol
                  sizeXs="12"
                  sizeSm="12"
                  sizeLg="6"
                  sizeMd="7"
                  sizeXl="4"
                  className="ion-align-self-center ion-text-center ion-margin-top"
                >
                  <IonIcon
                    className="alarm"
                    src="../../../../../assets/images/alarm.svg"
                  ></IonIcon>
                  <h3 className="ion-text-center ion-margin-bottom">
                    We will send you a nudge <br />
                    at 
                    <IonText className="c-orange">{reminderTime}</IonText>
                    everyday
                  </h3>
                </IonCol>
              </IonRow> 
              }
            { reminderTime === undefined ? 
              <IonRow className="ion-align-items-center ion-justify-content-center">
                <IonCol
                  sizeXs="10"
                  sizeSm="10"
                  sizeMd="7"
                  sizeLg="6"
                  sizeXl="4"
                  className="ion-align-self-center"
                >
                  <IonItem lines="none">
                    <IonLabel color="danger">
                      Yes, set a reminder. <br />
                      <IonText color="medium">(Push notification)</IonText>
                    </IonLabel>
                    <IonToggle mode="ios"
                    checked={checked}
                    onIonChange={e => setChecked(e.detail.checked)}
                    ></IonToggle>
                  </IonItem>
                </IonCol>
              </IonRow>
              : 
              <IonRow className="ion-align-items-center ion-justify-content-center">
                <IonCol
                  sizeXs="10"
                  sizeSm="10"
                  sizeMd="7"
                  sizeLg="6"
                  sizeXl="4"
                  className="ion-align-self-center"
                >
                  <IonItem lines="none">
                    <IonLabel color="danger">
                      Yes, set a reminder. <br />
                      <IonText color="medium">(Push notification)</IonText>
                    </IonLabel>
                    <IonToggle mode="ios"
                    checked={checked}
                    onIonChange={e => setChecked(e.detail.checked)}
                    ></IonToggle>
                  </IonItem>
                  <a className="c-orange ion-margin-top" onClick={setReminder}>Edit</a>

                </IonCol>
              </IonRow>
              }
{/* 
              <IonRow className="ion-align-items-center ion-justify-content-center">
                <IonCol
                  sizeXs="4"
                  sizeSm="4"
                  sizeMd="7"
                  sizeLg="6"
                  sizeXl="4"
                  className="IonText-center ion-align-self-center"
                >
                  <IonItem lines="none">
                    <IonToggle mode="ios"></IonToggle>
                  </IonItem>
                  <a href="#" className="c-orange ion-margin-top">
                    Edit
                  </a>
                </IonCol>
              </IonRow> */}
            </IonCol>
          </IonRow>
        </IonGrid>
      </IonContent>
    </IonPage>
  )
}

export default Reminder
